.btnSecundario {
    display: none;
}
a:hover {
    color: orangered;
}
.btnTransportista {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
    bottom: 5vw;
    border: none;
    padding: 0px 2vh;
}
.LappaLogo {
    display: flex;
    width: 20vh;
    transition: width 2s;
    margin-left: 20px;
}
.LappaLogo:hover{
    cursor: pointer;
}
.navBar {
    width: 100vw;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: white;
    padding: .3vw;
    border-bottom: black solid 1px;
    box-shadow:0vw 6px 7px black;
    margin-top: 0px;
    z-index: 100;
}