.container{
    display: flex;
    position: absolute;
    top: 20vh;
    left: 20vw;
    width: 60vw;
    height: 60vh;
    align-items: center;
    flex-direction: column;
    justify-content: space-evenly;

}


.pagina {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    text-align: center;
}

.transportistasList {
    overflow-y: scroll;
    max-height: 60vh;
    width: 80vh;
}
.transportistasList::-webkit-scrollbar {
    -webkit-appearance: none;
}

.transportistasList::-webkit-scrollbar:vertical {
    width:10px;
}

.transportistasList::-webkit-scrollbar-button:increment,.contenedor::-webkit-scrollbar-button {
    display: none;
}

.transportistasList::-webkit-scrollbar:horizontal {
    height: 10px;
}

.transportistasList::-webkit-scrollbar-thumb {
    background-color: #797979;
    border-radius: 20px;
}

.transportistasList::-webkit-scrollbar-track {
    border-radius: 10px;
}
.card {
    background-color: white;
    color: black;
    margin-bottom: 10px;
    padding: 10px;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
}